import React, { useEffect, useState } from "react";
import {makeMark, showVerticalMenu} from "./WorkFunctions";
import { Link, useParams } from "react-router-dom";

const Menu = () => {
    const { section, tutorial, article } = useParams();
    const [articles, setArticles] = useState({});
    const [chapters, setChapters] = useState({});
    const [error, setError] = useState(null);
    const [id, setId] = useState();

    useEffect(() => {
        fetch(`/content/${section}/${tutorial}/chapters.json`)
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                return response.json();
            })
            .then(chptrs => {
                setChapters(chptrs);
            })
            .catch(error => {
                console.error('Error fetching chapters:', error);
                setError(error);
            });
    }, [section, tutorial]);

    useEffect(() => {
        fetch(`/content/${section}/${tutorial}/articles.json`)
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                return response.json();
            })
            .then(data => {
                const currentArticle = data.find(art => art.name === article);
                setId(currentArticle.id);
                const groupedData = data.reduce((acc, article) => {
                    const key = article.chapter;
                    if (!acc[key]) {
                        acc[key] = [];
                    }
                    acc[key].push(article);
                    return acc;
                }, {});
                setArticles(groupedData);
            })
            .catch(error => {
                console.error('Error fetching articles:', error);
                setError(error);
            })
    }, [section, tutorial, article]);
    useEffect(() => {
        if (Object.keys(articles).length > 0 && Object.keys(chapters).length > 0) {
            setTimeout(() => {
                makeMark(id);
            }, 0);
        }
    }, [articles, chapters, id]);

    if (error) {
        return <div>Error: {error.message}</div>;
    }

    return (
        <>
            <button id="menu-button" className="menu-button" onClick={showVerticalMenu}>
                <span></span>
                <span></span>
                <span></span>
            </button>
            <aside className="vertical-menu-content" id="vertical-menu">
                {Object.keys(articles).map(chapter => (
                    <div key={chapter}>
                        <div className="chapter">
                            <b>{chapters[chapter]}</b>
                        </div>
                        {articles[chapter].map(article => (
                            <Link
                                id={article.id}
                                className="item"
                                to={`${article.url}`}
                                key={article.id}
                                >
                                <b>{article.title}</b>
                            </Link>
                        ))}
                    </div>
                ))}
            </aside>
        </>
    );
};

export default Menu;
